
.waveform .wavesurfer-handle {
  background-color: grey;
  width: 15%;
  max-width: 50px;
}
.button {
  border:none;
  padding: 0;
  font-weight: bold;
  width: 70px;
  height: 70px;
  border-radius: 100%;
  background-color: #9ddad8;
  text-align: center;
  cursor: pointer;
}
.time {
  margin: 10px;
  font-weight: bold;
  width: 70px;
  height: 70px;
  border-radius: 100%;
  background-color: #9ddad8;
  text-align: center;
  display: inline-block;
  font-size: 2em;
  line-height: 70px;
}
.editor {
  font-size: 0.8em;
}
.waveform {
  display:block;
}




